@import url(../fonts/stylesheet.css);

body,
html {
  overflow-x: hidden;
  font-family: 'PF Encore Sans Pro';
  cursor: url("../img/cursor.png"), progress;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

p {
  font-weight: 300;
}

li {
  list-style: none;
}

a {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

a:hover {
  text-decoration: none;
}

#top {
  display: none;
  position: fixed;
  right: 5%;
  bottom: 5%;
  cursor: pointer;
  z-index: 10;
  font-size: 60px;
  color: #fc635e;
}

.p-relative {
  position: relative;
}

.bg-nav {
  background: rgba(0, 0, 0, 0.891);
}

.active {
  color: #fc635e !important;
  border-bottom: 4px solid;
}

/* ########################### HEADER ###########################*/

.header {
  background: url("../images/bg-header.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.header-nav {
  z-index: 122;
  width: 100%;
  max-width: 100%;
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  text-transform: uppercase;
  color: #fff;
}

.header-nav-ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 10%;
  margin-bottom: 0;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.header-nav-li__item {
  font-weight: 300;
  color: #fff;
  border-bottom: transparent #fc635e;
}

.header-nav-li__item:hover {
  color: #fff;
}

.header-nav-logo {
  width: 50%;
}

.logo__title {
  text-transform: none;
  font-size: 24px;
  color: #fff;
  cursor: pointer;
  font-family: 'Gotham Pro';
  font-weight: 700;
}

.logo__title:hover {
  color: #fff;
}

.lines {
  position: relative;
}

.lines::before {
  position: absolute;
  content: '';
  width: 107px;
  height: 8px;
  background-color: #4f4d4d;
  display: block;
  top: 26px;
  left: -120px;
}

.lines::after {
  position: absolute;
  content: '';
  width: 107px;
  height: 8px;
  background-color: #4f4d4d;
  display: block;
  top: 26px;
  right: -122px;
}

.text {
  text-align: center;
  margin: 300px auto 169px;
  font-weight: 700;
}

.text__title {
  text-transform: uppercase;
  font-size: 51px;
  color: #fff;
}

.text__subtitle {
  color: #fff;
  font-size: 24px;
  font-weight: 300;
}

.text__link {
  display: inline-block;
  margin: 48px 0 0 0;
  font-size: 24px;
  color: #fc635e;
  border: 2px solid #fc635e;
  border-radius: 5px;
  padding: 18px 34px;
  font-weight: 300;
}

.text__link:hover {
  border-color: #fc635e;
  background: #fc635e;
  color: #fff;
}

.text-dashes {
  position: absolute;
  top: 76%;
}

span {
  color: #fc635e;
}

.pd {
  padding-bottom: 40px;
}

/* ########################### HEADER END ###########################*/

.title {
  margin: 67px auto 80px;
  text-align: center;
  color: #868686;
}

.title__h2 {
  font-weight: 500;
  font-size: 28px;
  display: inline-block;
  text-transform: uppercase;
  padding-bottom: 13px;
  border-bottom: 1px #868686 solid;
}

.title__p {
  font-weight: 300;
  font-size: 20px;
}

/* ########################### RECEIVE ###########################*/

.hexa {
  position: relative;
  margin-left: 142px;
  margin-top: 40px;
}

.hexagon {
  position: absolute;
}

.scale_0 {
  z-index: 10;
  -ms-transform: scale(0.6, 0.6);
  -sand-transform: rotate(0.6, 0.6);
  -webkit-transform: scale(0.6, 0.6);
  transform: scale(0.6, 0.6);
  left: 0px;
  top: 0px;
}

.scale_1 {
  z-index: 20;
  -ms-transform: scale(0.6, 0.6);
  -sand-transform: rotate(0.6, 0.6);
  -webkit-transform: scale(0.6, 0.6);
  transform: scale(0.6, 0.6);
  left: 0px;
  top: 0px;
}

.scale_2 {
  z-index: 30;
  -ms-transform: scale(0.58, 0.58);
  -sand-transform: rotate(0.58, 0.58);
  -webkit-transform: scale(0.58, 0.58);
  transform: scale(0.58, 0.58);
  left: 2px;
  top: 1px;
}

.scale_3 {
  z-index: 40;
  -ms-transform: scale(0.55, 0.55);
  -sand-transform: rotate(0.55, 0.55);
  -webkit-transform: scale(0.55, 0.55);
  transform: scale(0.55, 0.55);
  left: 4px;
  top: 2px;
}

.hexa {
  color: #524652;
  font-size: 90%;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: -moz-none;
  -ms-user-select: none;
  user-select: none;
  margin: auto;
  top: 25%;
  left: 31%;
}

.hexa_text {
  cursor: pointer;
  vertical-align: middle;
  position: absolute;
  z-index: 50;
  left: 0px;
  top: 0px;
  text-align: center;
  width: 102px;
  height: 60px;
}

.hexa_image {
  position: absolute;
  z-index: 60;
  width: 114px;
  left: -5px;
  height: 130px;
  top: -45px;
  background-position: 3px 5px;
  background-repeat: no-repeat;
}

.color_3 {
  background-color: #fc635e;
}

.color_2 {
  background-color: #fc635e;
}

.color_1 {
  background-color: #fc635e;
}

.h_sq {
  position: absolute;
  border-radius: 10px;
  width: 114px;
  height: 114px;
}

.t_sq_1 {
  left: 10px;
  top: 10px;
  z-index: 11;
  -sand-transform: rotate(120deg);
  -ms-transform: rotate(120deg);
  -webkit-transform: rotate(120deg);
  transform: rotate(120deg);
}

.t_sq_2 {
  left: 48px;
  top: 10px;
  z-index: 11;
  -sand-transform: rotate(60deg);
  -ms-transform: rotate(60deg);
  -webkit-transform: rotate(60deg);
  transform: rotate(60deg);
}

.t_sq_3 {
  left: 67px;
  top: -23px;
  z-index: 12;
}

.t_sq_4 {
  left: 10px;
  top: -57px;
  z-index: 13;
  -sand-transform: rotate(-120deg);
  -ms-transform: rotate(-120deg);
  -webkit-transform: rotate(-120deg);
  transform: rotate(-120deg);
}

.t_sq_5 {
  left: -9px;
  top: -23px;
  z-index: 10;
}

.t_sq_6 {
  left: 48px;
  top: -57px;
  z-index: 12;
  -sand-transform: rotate(-60deg);
  -ms-transform: rotate(-60deg);
  -webkit-transform: rotate(-60deg);
  transform: rotate(-60deg);
}

.receive {
  color: #868686;
}

.receive__h4 {
  text-transform: uppercase;
  font-size: 20px;
  display: inline-block;
  margin: 220px 0 20px 4px;
  font-weight: 500;
}

.receive__h4:after {
  margin: 12px auto 0;
  content: '';
  display: block;
  height: 1px;
  width: 58px;
  background-color: #868686;
}

.receive__p {
  font-weight: 300;
  font-size: 15px;
  margin-bottom: 60px;
}

.receive-card:hover:hover .figure-icon,
.receive-card:hover .color_2 {
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
  color: #fc635e;
}

.receive-card:hover:hover .color_3 {
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
  background: #fff;
}

.figure-icon {
  font-size: 35px;
  color: #fff;
}

/* ########################### RECEIVE END ###########################*/

/* ########################### EXPAND ###########################*/

.expand {
